/* archive page */
.post-full-content .archive-list h2 {
    margin-bottom: 20px;
}

.archive-list ul {
    list-style: none;
}

.archive-list ul li {
    border-bottom: 1px solid #d8d8d8;
    padding-bottom: 25px;
}

.archive-list li .post-info {
    font-size: 1.6rem;
    overflow: hidden;
}

.archive-list li .post-date {
    float: left;
    line-height: 1.2;
}

.archive-list li .tag {
    padding: 3px 15px;
    border: 1px solid #d8d8d8;
    border-radius: 30px;
    float: right;
    line-height: 1.2;
}

.post-full-content .archive-list li a {
    display: block;
    box-shadow: none;
    overflow: hidden;
}

.post-full-content .archive-list li a img {
    width: 172px;
    height: 172px;
    margin: 0;
    float: left;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.post-full-content .archive-list li a h3 {
    margin-top: 0;
}

.post-full-content .archive-list li a pre {
    display: none;
}

.post-full-content .archive-list li .archive-item-content {
    white-space: normal;
    word-wrap: break-word;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    max-height: 135px;
    max-width: 575px;
    overflow: hidden;
}

.post-full-content .archive-list li .archive-item-right {
    float: left;
    margin-left: 20px;
}

@media (max-width: 1280px) {
    .archive-list li .post-date {
        padding-top: 10px;
    }

    .post-full-content .archive-list li a img {
        float: none;
        width: 100%;
        height: auto;
        margin-top: 20px;
    }

    .post-full-content .archive-list li .archive-item-right {
        margin-top: 15px;
        margin-left: 0;
        float: none;
    }

    .post-full-content .archive-list li .archive-item-content {
        max-width: 100%;
    }
}

/* Tage page */
.post-tags-list .post-tags-kind {
    list-style: none;
}
.post-tags-list .post-tags-kind li {
    display: inline-block;
}
.post-tags-list .post-tags-kind li a {
    padding: 5px 18px;
    border: 1px solid #d8d8d8;
    border-radius: 25px;
    box-shadow: none;
}

.social-link-instagram {
    padding-left: 5px;
    padding-right: 5px;
}

.tag-card {
    padding: 10px;
}

.tag-card h2 {
    text-align: center;
}

.social-link-instagram img {
    width: 30px;
    height: 30px;
    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(93deg) brightness(103%) contrast(103%);
}